.playerCards {
float: left;
border-spacing: 22px 10px;
position: relative;
left: -4px;

}

.opponentCards {
  float: right;
  border-spacing: 22px 10px;
  position: relative;


}
.playingArea{
  position: relative;
  height:350px;
  width:595px;
  margin: auto;
  top: -43px;



}

.tableData{
  height: 84px;
  width: 65px;
  text-align: center;
  padding:0;

}

.points {

  height: 25px;
  width: 125px;

  position: relative;
  margin: auto;
  top: 43px;
  left: -2px;
}

.playerPoints{
  float: left;
  width: 50%;
  height:100%;
  text-align: center;
  color: white;
  font-size: 20px;
}

.playerHand {
  float:left;
  height: 100%;
  width: 50%;
  border-spacing: 12px 0px;
  position: relative;
  right: 14px;


}

.oppHand{
  float: right;
  height: 100%;
  width: 50%;
  border-spacing: 12px 0px;
  position: relative;
  left: 8px;
}

.hands{
  position: relative;
  height:90px;
  width:715px;
  margin: auto;
  clear: both;
  top:-62px;


}

.oppPoints{
  float:right;
  width:50%;
  height: 100%;
  text-align: center;
  color: white;
  font-size: 20px;
}

.names {
  width:75%;
  height: 50px;
  position: relative;
  margin: auto;
}

.playerName {
  float:left;
  width:40%;
  height: 75%;
  text-align: center;
  color: white;
  position: relative;
  top:10px;
  font-size: 20px;
}

.oppName{
  float: right;
  width: 40%;
  height: 75%;
  text-align: center;
  color: white;
  position: relative;
  top:10px;
  font-size: 20px;
}

.score {
  width:710px;
  height: 45px;
  margin: auto;
  position: relative;
  top: -36px;
  left: -2px;


}

.playerScore{
  width:6%;
  float: left;
  height: 50%;
  text-align: center;
  color: white;
  font-size: 20px;
  position: relative;
  top:10px;

}

.oppScore{
  width:6%;
  float: right;
  height:50%;
  text-align: center;
  color: white;
  font-size: 20px;
  position: relative;
  top:10px;
}

.controlPanel {
  width: 320px;
  height: 60px;
  position: relative;
  left: 60px;
  top:-10px;
}

.standIndicator{
  position: relative;
  top: -435px;
  margin: auto;
  clear: both;
  width: 595px;
  height: 20px;
}

.newGame{
  height: 150px;
  width:400px;
  background-color: blue;
  margin: auto;
  position: relative;
  top: -500px;
  color: white;
  text-align: center;
}
.enterName {
  margin:auto;
  background-color: red;
  margin-top: auto;
  position: relative;
  top: 46%
}

.winner{
  height: 150px;
  width:400px;
  margin: auto;
  position: relative;
  top: -500px;
  color: white;
  text-align: center;
}



